// MEDIA QUERIES
@mixin desktop {
    @media (max-width: $desktop) {
      @content;
    }
  }

@mixin desktop-small {
  @media (max-width: $desktop-small) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: $tablet) {
    @content;
  }
}

@mixin tablet-small {
  @media (max-width: $tablet-small) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: $mobile) {
    @content;
  }
}
@mixin min-mobile {
  @media (min-width: $mobile) {
    @content;
  }
}
